.projects {
  margin-top: 50px;
  padding: 0 40px;
}

.title {
  font-size: 2.5rem;
}

.list {
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

@media (max-width: 1024px) {
  .list {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 675px) {
  .list {
    grid-template-columns: repeat(1, 1fr);
    place-items: center;
  }
}
